import { HvIconSprite } from "@hitachivantara/uikit-react-icons";

export type IconUiKitProps = {
  name: string;
};

const spriteUri = import.meta.resolve?.(
  "@hv/uikit-icons/icons.svg"
) as unknown as string;

const IconUiKit = ({ name }: IconUiKitProps) => {
  return <HvIconSprite spriteUrl={spriteUri} iconName={name} />;
};

export default IconUiKit;
